.home {
  padding: 30px;
}

.stock-search {
  .condition {
    &:nth-child(1) {
      width: 20%;
    }
    &:nth-child(2) {
      padding-left: 10px;
      width: 80%;
      input {
        &:nth-child(1) {
          width: 30%;
        }
        &:nth-child(2) {
          margin-left: 5px;
          width: 20%;
        }
        &:nth-child(3) {
          margin-left: 5px;
          width: 25%;
        }
      }
      i {
        margin-right: 5px;
      }
    }

    &:nth-child(3) {
      width: 100%;
      margin-top: 20px;
    }
  }
  .condition-list {
    display: flex;
    flex-wrap: wrap;
  }
  .condition-item {
    width: 19%;
    margin: 0 0.5%;
    border: 1px black solid;
    border-radius: 5px;
    padding: 7px 8px;
    background-color: whitesmoke;
    display: flex;
    justify-content: space-around;
  }
  .method {
    width: 100%;
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
}

.tipsText {
  color: red;
  font-size: 12px;
}
